<template>
  <div ref="recommedJobs" class="section">
    <div class="container">
      <h3 class="section__title ps-3">推薦職位</h3>
      <swiper
        v-if="hotJobs.length > 0"
        :options="swiperOption"
      >
        <swiper-slide v-for="item in hotJobs" :key="item.id">
          <GoodJobCard :good-job="item"></GoodJobCard>
        </swiper-slide>
        <div class="swiper-button-prev swiper-custom">
          <button type="button" class="btn btn-lg rounded-circle iconBtnBox">
            <i class="bi bi-arrow-left actionIcon"></i>
          </button>
        </div>
        <div class="swiper-button-next swiper-custom">
          <button type="button" class="btn btn-lg rounded-circle iconBtnBox">
            <i class="bi bi-arrow-right actionIcon"></i>
          </button>
        </div>
      </swiper>
    </div>
  </div>
  <div ref="jobsListContainer" class="jobsListContainer container">
    <h3 class="section__title ps-3">全部職位</h3>
    <div class="row" v-if="jobsList.length">
      <div class="col-lg-6 col-12">
        <div class="jobListBox">
          <div class="d-flex justify-content-between align-items-center ps-3 mb-3">
            <div class="d-flex flex-wrap">
              <p class="text-primary me-2" v-if="filterTxt !== ''">
                <span class="text-secondary">搜尋條件：</span>{{ filterTxt }}
              </p>
              <p class="text-secondary fw-normal  text-nowrap">共 {{ jobsList.length }} 個職位</p>
            </div>
            <select
              class="form-select form-select-lg w-auto border-0 text-gray-dark"
              @change="changeJobSort($event)"
              v-model="sortWay"
            >
              <option selected value="time">最新至最舊</option>
              <option value="money">薪水高至低</option>
            </select>
          </div>
          <ul ref="jobList" class="allJobList">
            <template v-for="item in nowPageJobs">
              <li
                :key="item.id"
                @click="selectJob(item.id)"
                :data-id="item.id"
                v-if="mountState"
                :ref="`list__item--${item.id}`"
                class="list__item d-flex box--shadow flex-column align-items-start pe-auto"
              >
                <button
                  class="collectBtn btn btn-outline-gray-line position-absolute"
                  type="button"
                >
                  <i class="jobIcon bi bi-bookmark-fill"></i>
                </button>
                <div class="list__item__tagBox mb-3">
                  <button
                    type="button"
                    class="jobTag btn"
                    @click="searchByJobCategory(item.category)"
                  >
                    {{ item.category }}
                  </button>
                </div>
                <div class="d-flex w-100 flex-md-row flex-column">
                  <div class="list__item__imgBox">
                    <img class="jobImage" :src="item.imageUrl" :alt="item.title + '職位圖片'" />
                    <div class="img-cover d-md-none"></div>
                    <div class="list__item__logoImgBox">
                      <img
                        class="logoImg"
                        :src="item.options.company.companyLogoUrl"
                        :alt="jobItem.options.company.companyName + 'logo'"
                      />
                    </div>
                  </div>
                  <div
                    class="list__item__txtBox
                  flex-grow-1 d-flex flex-column
                  justify-content-between"
                  >
                    <div class="mb-3 d-flex flex-column align-items-start">
                      <router-link
                        class="list__item__title mb-3 me-7 pe-auto"
                        :to="`/products-list/product/${item.id}`"
                        >{{ item.title }}</router-link
                      >
                      <router-link
                        class="page__link subTxt  mb-2 me-7 pe-auto"
                        :to="`/products-list/company/${item.options.company.companyLink}`"
                        >{{ item.options.company.companyName }}</router-link
                      >
                    </div>
                    <div class="d-flex justify-content-between align-items-center">
                      <p class="text-primary" v-if="!item.options.job.salaryInterView">
                        {{ item.price }} / 月薪
                      </p>
                      <p class="text-primary" v-if="item.options.job.salaryInterView">薪資面議</p>
                      <p class="subTxt text-secondary">
                        {{ $filters.date(item.options.job.create) }}
                      </p>
                    </div>
                  </div>
                </div>
              </li>
            </template>
          </ul>
        </div>
      </div>
      <div class="col-lg-6 col-12 d-lg-block d-none">
        <div
          ref="jobSelectBox"
          class="sideJobBox sideJobBox--sticky box--shadow"
          v-if="mountState"
        >
          <div class="d-flex justify-content-between align-items-center mb-3">
            <div class="mb-3">
              <button
                type="button"
                class="jobTag btn"
                @click="searchByJobCategory(jobItem.category)"
              >
                {{ jobItem.category }}
              </button>
            </div>
            <div class="d-flex">
              <router-link
                class="btn btn-outline-gray-line btn-bg text-dark d-flex align-items-center me-2"
                type="button"
                :to="`/products-list/product/${jobItem.id}`"
                >開新頁面</router-link
              >
              <button class="collectBtn btn btn-outline-gray-line" type="button">
                <i class="jobIcon bi bi-bookmark-fill"></i>
              </button>
            </div>
          </div>
          <div class="pb-5 border-bottom border-gray-line">
            <div class="d-flex mb-3">
              <div class="sideJobBox__imgBox">
                <img class="jobImage" :src="jobItem.imageUrl" :alt="`${jobItem.title}職位圖片`" />
                <div class="logoImageBox">
                  <img
                    class="logoImage"
                    :src="jobItem.options.company.companyLogoUrl"
                    :alt="`${jobItem.options.company.companyName}logo`"
                  />
                </div>
              </div>
              <div class="sideJobBox__txtBox">
                <div>
                  <router-link
                    class="sideJobBox__title mb-3 d-block"
                    type="button"
                    :to="`/products-list/product/${jobItem.id}`"
                    >{{ jobItem.title }}</router-link
                  >
                  <router-link
                    class="page__txt page__link subTxt  mb-4 d-block"
                    type="button"
                    :to="`/products-list/company/${jobItem.options.company.companyLink}`"
                    >{{ jobItem.options.company.companyName }}</router-link
                  >
                </div>
                <div class="d-flex">
                  <p class="page__txt me-5 subTxt">
                    <span><i class="jobIcon--sm me-1 bi bi-geo-alt"></i></span
                    >{{ jobItem.options.company.companyAddressCity }}
                  </p>
                  <p class="page__txt subTxt">
                    <span><i class="jobIcon--sm me-1 bi bi-people"></i></span>需求人數：{{
                      jobItem.num
                    }}
                  </p>
                </div>
              </div>
            </div>
            <div class="d-flex justify-content-between align-items-center">
              <p class="text-primary fw-bold" v-if="!jobItem.options.job.salaryInterView">
                {{ jobItem.price }} / 月薪
              </p>
              <p class="text-primary fw-bold" v-if="jobItem.options.job.salaryInterView">
                薪資面議
              </p>
              <div class="d-flex align-items-center">
                <p class="subTxt text-secondary me-2">
                  {{ $filters.date(jobItem.options.job.create) }}
                </p>
                <router-link
                  class="btn--applyJob btn btn-primary btn-bg d-flex align-items-center me-2"
                  type="button"
                  :to="`/apply-job/${jobItem.id}`"
                  >申請</router-link
                >
              </div>
            </div>
          </div>
          <div class="sideJobBox__section">
            <h3 class="section__title--sub"><span class="title__icon"></span>職位內容</h3>
            <p class="mb-3">
              <i class="jobIcon--sm me-1 bi bi-journal"></i>工作性質：{{
                jobItem.options.job.workType
              }}
            </p>
            <p class="mb-3">
              <span><i class="jobIcon--sm me-1 bi bi-clock"></i></span>工作時間：{{
                jobItem.options.job.workTime
              }}
            </p>
            <p class="mb-3">
              <span><i class="jobIcon--sm me-1 bi bi-building"></i></span>產業類別：{{
                jobItem.options.company.industryCategory
              }}
            </p>
            <p class="mb-3">
              <span><i class="jobIcon--sm me-1 bi bi-card-list"></i></span>工作類別：{{
                jobItem.category
              }}
            </p>
            <p class="mb-3">工作內容：</p>
            <div class="page__txt" v-html="jobItem.content"></div>
          </div>
          <div class="sideJobBox__section">
            <h3 class="section__title--sub"><span class="title__icon"> </span>應徵條件</h3>
            <p class="mb-3">
              <span><i class="jobIcon--sm me-1 bi bi-book"></i></span>學歷要求：{{
                jobItem.options.job.education
              }}
            </p>
            <p class="mb-3">
              <span><i class="jobIcon--sm me-1 bi bi-briefcase"></i></span>工作經驗：{{
                jobItem.options.job.workExp
              }}
            </p>
            <p class="mb-3">其他條件：</p>
            <div class="page__txt" v-html="jobItem.options.job.otherRequirement"></div>
          </div>
          <div class="sideJobBox__section">
            <h3 class="section__title--sub"><span class="title__icon"> </span>申請方法</h3>
            <p class="mb-3">
              <span><i class="jobIcon--sm me-1 bi bi-person"></i></span>職位聯絡人：{{
                jobItem.options.company.companyContact
              }}
            </p>
            <p class="mb-3">
              <span><i class="jobIcon--sm me-1 bi bi-envelope"></i></span>聯絡信箱：{{
                jobItem.options.company.companyEmail
              }}
            </p>
            <p class="mb-3">
              <span><i class="jobIcon--sm me-1 bi bi-phone"></i></span>聯絡電話：{{
                jobItem.options.company.companyTel
              }}
            </p>
            <p class="mb-3">申請備註：</p>
            <div class="page__txt" v-html="jobItem.options.job.otherApplyInfo"></div>
          </div>
        </div>
      </div>
    </div>
  </div>

  <PagenationModal :jobs-list="jobsList" @change-page="changePage" />
  <div class="sideBtnBox">
    <FilterBtn :tem-filter-data="filterData" @send-filter-data="filter" />
    <UpTopBtn />
  </div>
</template>

<script>
import emitter from '@/methods/emitter';
import searchFilter from '@/methods/searchFilter';
import webData from '@/methods/webData';
import PagenationModal from '@/components/helpers/Pagenation.vue';
import GoodJobCard from '@/components/front/GoodJobCard.vue';
import { Swiper, SwiperSlide } from 'swiper/vue';
import UpTopBtn from '@/components/helpers/UpTopBtn.vue';
import FilterBtn from '@/components/helpers/FilterBtn.vue';
import SwiperCore, { Autoplay, Pagination, Navigation } from 'swiper/core';

SwiperCore.use([Autoplay, Pagination, Navigation]);

export default {
  components: {
    PagenationModal,
    GoodJobCard,
    Swiper,
    SwiperSlide,
    UpTopBtn,
    FilterBtn,
  },
  data() {
    return {
      fullWidth: 0,
      fullHeight: 0,
      products: [],
      jobsList: [],
      hotJobs: [],
      nowPageJobs: [],
      jobItem: {
        options: {
          company: {
            companyImagesUrl: [],
          },
          job: {},
        },
      },
      searchFilterMethods: {},
      pageNumber: 1,
      sortWay: 'time',
      formData: {},
      filterData: {
        keyword: '',
        city: '不限',
        industryCategory: '不限',
        jobCategory: '不限',
        workExp: '不限',
        education: '不限',
        workType: '不限',
        workTime: '不限',
        salaryLow: null,
        salaryHight: null,
        salaryInterView: false,
      },
      // 篩選框顯示與否狀態
      filterBoxState: false,
      filterTxt: '',
      mountState: false,
      // swiper
      swiperNum: 1,
      swiperOption: {
        autoplay: {
          delay: 2500,
          disableOnInteraction: false,
        },
        pagination: {
          clickable: true,
        },
        loop: true,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
        slidesPerView: this.swiperNum,
        spaceBetween: '20',
      },
    };
  },
  watch: {
    fullWidth(newValue) {
      if (newValue > 768) {
        this.swiperNum = 3;
      } else if (newValue > 576) {
        this.swiperNum = 2;
      } else {
        this.swiperNum = 1;
      }
    },
  },
  computed: {
    // 所有企業
    sortCompany() {
      const temCompanyArray = [];
      if (this.products.length > 1) {
        this.products.forEach((item) => {
          if (item.description === '企業') {
            temCompanyArray.push(item);
          }
        });
      }
      return temCompanyArray;
    },
  },
  methods: {
    // 打開篩選彈跳視窗
    openSideFilterBox() {
      if (this.filterBoxState) {
        this.filterBoxState = false;
      } else if (!this.filterBoxState) {
        this.filterBoxState = true;
      }
    },
    // 根據職位類別搜尋
    searchByJobCategory(jobCategory) {
      const keyword = '不限';
      const city = '不限';
      this.$router.push(`/search/?keyword=${keyword}&city=${city}&jobCategory=${jobCategory}`);
    },
    // 調整列表排序方式
    changeJobSort() {
      if (this.sortWay === 'time') {
        this.jobsList.sort((a, b) => b.options.job.create - a.options.job.create);
      } else if (this.sortWay === 'money') {
        this.jobsList.sort((a, b) => b.price - a.price);
      }
    },
    // 切換頁面
    changePage(nowPageNum) {
      this.pageNumber = nowPageNum;
      this.getNowPageJobs();
    },
    // 篩選查詢
    filter(value) {
      this.filterData = value;
      emitter.emit('spinner-open');
      this.classifyJob();
      const temArray = this.searchFilterMethods.filter(this.jobsList, this.filterData);
      this.jobsList = temArray;
      this.changePage(1);
      this.getfilterTxt();
      emitter.emit('filterBtn-close');
      emitter.emit('spinner-close');
    },
    // 取得搜尋條件文字
    getfilterTxt() {
      const keyWord = this.filterData.keyword === '' ? '' : `${this.filterData.keyword}、`;
      const city = this.filterData.city === '不限' ? '' : `${this.filterData.city}、`;
      const industryCategory = this.filterData.industryCategory === '不限' ? '' : `${this.filterData.industryCategory}、`;
      const jobCategory = this.filterData.jobCategory === '不限' ? '' : `${this.filterData.jobCategory}、`;
      const workExp = this.filterData.workExp === '不限' ? '' : `${this.filterData.workExp}、`;
      const education = this.filterData.education === '不限' ? '' : `${this.filterData.education}、`;
      const workType = this.filterData.workType === '不限' ? '' : `${this.filterData.workType}、`;
      const workTime = this.filterData.workTime === '不限' ? '' : `${this.filterData.workTime}、`;
      const salaryLow = this.filterData.salaryLow === null ? '' : `最低薪資${this.filterData.salaryLow}、`;
      const salaryHight = this.filterData.salaryHight === null ? '' : `最高薪資${this.filterData.salaryHight}、`;
      const salaryInterView = this.filterData.salaryInterView === false ? '' : '薪資面議、';
      const temTxt = keyWord
        + city
        + industryCategory
        + jobCategory
        + workExp
        + education
        + workType
        + workTime
        + salaryLow
        + salaryHight
        + salaryInterView;
      if (temTxt.length > 1) {
        this.filterTxt = temTxt.slice(0, temTxt.length - 1);
      } else {
        this.filterTxt = temTxt;
      }
    },
    // 清除篩選條件
    cleanFilter() {
      this.searchFilterMethods.getFilterData(this.filterData);
      this.searchFilterMethods.cleanFilter();
      this.filterData = this.searchFilterMethods.returnFilter();
    },
    // 點擊卡片：pc->選擇右側職位，pad->跳轉至該職位頁面
    selectJob(id) {
      if (this.fullWidth > 991) {
        this.nowPageJobs.forEach((item) => {
          this.$refs[`list__item--${item.id}`].classList.remove('active');
          if (item.id === id) {
            this.jobItem = item;
            this.$refs[`list__item--${item.id}`].classList.add('active');
          }
        });
        this.$refs.jobSelectBox.scrollTop = 0;
      } else {
        this.$router.push(`/products-list/product/${id}`);
      }
    },
    selectJobFrist(id) {
      if (this.fullWidth > 991) {
        this.nowPageJobs.forEach((item) => {
          this.$refs[`list__item--${item.id}`].classList.remove('active');
          if (item.id === id) {
            this.jobItem = item;
            this.$refs[`list__item--${id}`].classList.add('active');
          }
        });
        this.$refs.jobSelectBox.scrollTop = 0;
      }
    },
    // 本頁職位
    getNowPageJobs() {
      const temPageJobs = [];
      if (this.jobsList.length !== []) {
        const pageFrist = this.pageNumber * 10 - 10;
        this.jobsList.forEach((item, index) => {
          if (pageFrist <= index && index < this.pageNumber * 10) {
            temPageJobs.push(item);
          }
        });
        document.documentElement.scrollTop = 0;
        [this.jobItem] = temPageJobs;
      }
      this.nowPageJobs = temPageJobs;
      // 時間差，拜託助教幫忙看這個還有啥解法
      setTimeout(() => {
        this.selectJobFrist(this.nowPageJobs[0].id);
      }, 10);
    },
    // 熱門職位
    sortHotJobs() {
      this.hotJobs = [];
      if (this.jobsList.length > 0) {
        const temGoodJob = this.jobsList.filter((item) => item.options.job.promote === 1);
        const arr = new Set([]);
        for (let index = 0; arr.size < 6; index += 1) {
          const num = Math.floor(Math.random() * temGoodJob.length);
          arr.add(num);
        }
        arr.forEach((i) => {
          this.hotJobs.push(temGoodJob[i]);
        });
      }
    },
    // 篩選出所有職位
    classifyJob() {
      this.jobsList = [];
      this.changePage(1);
      this.products.forEach((item) => {
        if (item.description === '職位') {
          const Obj = item;
          this.sortCompany.forEach((temCompany) => {
            if (Obj.options.company.companyName === temCompany.title) {
              Obj.options.company.companyLink = temCompany.id;
            }
          });
          this.jobsList.push(Obj);
        }
      });
      this.changeJobSort();
      if (!this.hotJobs.length) {
        this.sortHotJobs(); // 不用再重新撈一次
      }
      this.getNowPageJobs();
    },
    // 抓全部資料
    getOgData() {
      emitter.emit('spinner-open');
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/products/all`;
      this.$http
        .get(url)
        .then((res) => {
          this.products = res.data.products;
          emitter.emit('spinner-close');
          this.classifyJob();
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  created() {
    this.getOgData();
    this.formData = webData;
    this.searchFilterMethods = searchFilter;
    emitter.emit('spinner-open-bg', 1000);
  },
  mounted() {
    this.mountState = true;
    const vm = this;
    vm.fullWidth = window.innerWidth;
    vm.fullHeight = window.innerHeight;
    window.onresize = () => {
      vm.fullWidth = window.innerWidth;
      vm.fullHeight = window.innerHeight;
    };
  },
};
</script>

<style lang="scss"></style>
